html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-decor {
  width: 1.54em;
  height: 1em;
  fill: #00AAEF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 2.42em;
  height: 1em;
  fill: initial;
}

.nav .nav__link {
  display: inline-block;
  position: relative;
  overflow: hidden;
  padding: 3px 5px;
  line-height: 1;
}

.nav .nav__link:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background: #1bb4d0;
  transform: translate3d(102%, 0, 0);
  transition: transform 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.nav .nav__link:hover::after {
  transform: translate3d(0, 0, 0);
}

.nav .nav__link:hover .nav__t:before {
  width: 100%;
}

.nav .nav__t {
  display: block;
  position: relative;
}

.nav .nav__t:before {
  content: attr(data-letters);
  position: absolute;
  color: #ffffff;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
  width: 0%;
  transition: width 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

body {
  background: #FFF;
  font-family: "Ubuntu",sans-serif;
}

.textual {
  font-size: 16px;
  line-height: 1.5;
  color: #000;
}

.textual h2 {
  font-size: 32px;
  line-height: 1.1;
  font-weight: normal;
  margin-bottom: 1em;
}

.textual p {
  margin-bottom: 1em;
}

.textual ol {
  list-style: decimal;
  position: relative;
  left: -5px;
}

@media only screen and (max-width: 720px) {
  .textual ol {
    margin-left: 2em;
  }
}

.textual ol li {
  padding-left: 5px;
}

.textual ol, .textual ul {
  margin-bottom: 1em;
}

.textual_big {
  font-size: 24px;
  line-height: 1.3;
}

.textual_big a {
  text-decoration: none;
  color: #000;
}

.form__field {
  margin-bottom: 10px;
}

.input, .textarea {
  text-align: center;
  line-height: normal;
  height: 52px;
  background: #eeeeee;
  font-weight: 600;
  color: #393939;
  text-transform: uppercase;
  width: 100%;
}

.input:placeholder, .textarea:placeholder {
  font-size: 14px;
}

.textarea {
  height: 100px;
  padding: 20px;
  border: none;
}

.button {
  font-size: 14px;
  line-height: 52px;
  height: 52px;
  display: block;
  background: #1cabec;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  width: 100%;
}

.button_inline {
  display: inline-block;
  padding: 0 60px;
  width: auto;
}

.icon-logo {
  fill: #1bb4d0;
  width: 7.865em;
  height: 3.25em;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 20px;
  *zoom: 1;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

.header {
  margin-top: 20px;
}

.header__row {
  display: flex;
  justify-content: space-between;
}

.lang__list {
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
  border: 1px solid #000000;
}

.lang__item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.welcome {
  position: relative;
  height: 500px;
  height: calc(100vh - 200px);
  margin-top: 60px;
  min-height: 300px;
}

.welcome__container {
  height: 100%;
}

.welcome__video {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.welcome__placeholder {
  height: 100%;
  background: #b1b1b1;
}

.scroll-down {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 40px;
  height: 40px;
  bottom: -10px;
  transform: translateY(100%);
}

.scroll-down i {
  position: absolute;
  top: 0;
  left: 0;
  background: #1bb4d0;
}

.scroll-down i:nth-child(1), .scroll-down i:nth-child(3) {
  width: 100%;
  height: 1px;
}

.scroll-down i:nth-child(2), .scroll-down i:nth-child(4) {
  width: 1px;
  height: 100%;
}

.scroll-down i:nth-child(3) {
  top: auto;
  bottom: 0;
}

.scroll-down i:nth-child(2) {
  left: auto;
  right: 0;
}

#bgvid {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  background: url("//demosthenes.info/assets/images/polina.jpg") no-repeat;
  background-size: cover;
  transition: 1s opacity;
}

.nav__item {
  display: inline-block;
  text-transform: uppercase;
  margin: 0 10px;
}

.nav__link {
  text-decoration: none;
  color: #000000;
}

.nav__link:hover {
  color: #1bb4d0;
}
